<template>
  <section class="topbar">
    <b class="btn iconfont icon-back" @click='back'></b>
    <slot name='text'></slot>
    <slot name='right'></slot>
  </section>
</template>
<script>
  export default {
    name: '',
    props:{
      comeBack:{
        default:false
      }
    },
    data () {
      return {}
    },
    methods: {
      back(){
        if(!this.comeBack){
          window.history.length > 1
          ? this.$router.go(-1)
          : this.$router.push('/')
        }else{
          this.comeBack();
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  .topbar {
    background-color: #fff;
    text-align: center;
    font-size: 0.32rem;
    line-height: 0.8rem;
    height: 0.8rem;
    border-bottom: solid #e5e5e5 0.02rem;
    position: relative;
    .btn {
      width: 0.7rem;
      height: 100%;
      display: inline-block;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      color:rgb(115,115,115);
      &.iconfont {
        font-size: 0.42rem;
      }
    }
  }
</style>



