<template>   
    <ul v-if='Lists.length>0'>
      <li class='listCon' v-for='listInfo in Lists' :key='listInfo.productId'>
        <div class='list' @click='$router.push(`/productArticleDetailsPage/${listInfo.productId}`)'>
          <div class="listImg" :style="{backgroundImage:'url('+listInfo.preimg+')'}"></div>
          <div class='listInfo'>
            <div class='listTitle' v-text='listInfo.title'></div>
            <div class='listName'>{{listInfo.nickname}}<div><span :class="[listInfo.userType==3&&'blue',listInfo.userType==4&&'red']">{{showUserType(listInfo.userType)}}</span></div></div>
            <div class='listText'  v-text='listInfo.desc'></div>
          </div>
        </div>
      </li>
    </ul>
</template>
<script>
  export default {
    name: '',
    components: {},
    props: ['Lists'],
    data () {
      return {}
    },
    watch: {},
    computed: {
     
    },
    mounted () {
      },
    methods: {
      showUserType(data){
        switch (data) {
          case 2:
            return '达人';
            break;
          case 3:
            return '教练';
            break;
          case 4:
            return '俱乐部';
            break;
          default:
            break;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
      .listCon{
        width:6.9rem;
        height:2.8rem;
        margin:0 0.3rem;
        border-bottom: 1px solid rgb(229,229,229);
        .list{
          width:100%;
          height:2rem;
          padding-top:0.5rem;
          display:flex;
          .listImg{
            width:2.6rem;
            height:100%;
            background-position: center center;
            background-size: cover;
            margin-right:0.23rem;
          }
          .listInfo{
            display:flex;
            justify-content: space-between;
            flex:1;
            overflow:hidden;
            flex-direction: column;
            .listTitle{
              font-size:0.36rem;
              font-weight: bold;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .listName{
              color:#888888;
              font-size:0.28rem;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              div{
                display: inline-block;
                margin-left: 0.1rem;
                span{
                  padding: 0 0.15rem;
                  border-radius: 0.36rem; 
                  color: white;
                  background: #edaa36;
                  &.red{
                    background: #d33c44;
                  }
                  &.blue{
                    background: #1797c6;
                  }
                }
              }
            }
            .listText{
              font-size:0.28rem;
              line-height:initial;
              color:#555555;
              display: -webkit-box;
              overflow: hidden;
              text-overflow: ellipsis;
              word-wrap: break-word;
              white-space: normal !important;
              -webkit-line-clamp: 2;
              /*! autoprefixer: off */
              -webkit-box-orient: vertical;
              /* autoprefixer: on */
            }
          }
        }
      }
</style>


