<!-- by 添少 -->
<template>
  <section class="topbar">
    <div>
      <slot name="before"></slot>
    </div>
    <div>
      <slot></slot>
    </div>
    <div>
      <slot name="after"></slot>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'topbar'
  }
</script>

<style lang="scss" scoped>
  .topbar {
    background-color: #fff;
    text-align: left;
    font-size: 16px;
    line-height: 40px;
    height: 40px;
    border-bottom: solid #e5e5e5 1px;
    position: relative;
    display: flex;
    > * {
      box-sizing: border-box;
    }
    > :nth-child(1),
    > :nth-child(3) {
      flex: 0 0 30%;
    }
    > :nth-child(2) {
      text-align: center;
      flex: 1;
    }
    > :nth-child(3) {
      text-align: right;
      padding-right: 15px;
    }
    .iconfont {
      font-size: 21px;
      height: 100%;
      display: inline-block;
      padding-left: 10px;
      padding-right: 10px;
    }
    .btn {
      background-color: #1797c6;
      vertical-align: middle;
      font-size: 12px;
      line-height: 21px;
      color: #fff;
      padding: 0 0.5em;
      border-radius: 2px;
      border: none;
      display: inline-block;
    }
  }
</style>



