<template>
  <transition name="fade" mode="in-out">
    <section class='commentCon'>
      <v-Header :comeBack='comeBack'>
        <span slot='text'>评论</span>
      </v-Header>
      <textarea placeholder='说点什么好呢...' class='commetText' v-model='commetText'></textarea>
      <div class='submitBtn' @click='sendCommet'>提交评论</div>
      <mt-popup class='popup' v-model="popupVisible" :modal=false position="top">提交失败</mt-popup>
    </section>
  </transition>
</template>
<script>
import Header from '../../components/header';
import { Indicator } from 'mint-ui';
  export default {
    name: '',
    components: {'v-Header':Header},
    props: {
      comeBack:{},
      articleId:{},
      atUserId:{},
      isforumPage:{
        default:false
      }},
    data () {
      return {
        commetText:'',
        isLoading:false,
        popupVisible:false
      }
    },
    watch: {},
    computed: {},
    created () {
      
    },
    methods: {
      sendCommet(){
        if(this.isLoading||this.commetText.length==0){return;}
        this.isLoading = true;
        Indicator.open({
          text: '正在发表...'
        });
        let sendMessage = `${this.$port.COMMENT_ADD_PROT}?contId=${this.articleId}&atUserId=${this.atUserId}&comment=${this.commetText}`;
        if(this.atUserId != 0 && !this.isforumPage){
          sendMessage = `${this.$port.COMMENT_ADD_REPLY}?commentId=${this.articleId}&atUserId=${this.atUserId}&commentReply=${this.commetText}`
        }
        this.$http.post(sendMessage,{
        }).then((response) => {
          this.isLoading = false;
          const {data} = response;
          Indicator.close();
          if(response.code == 200){
            console.log(data);
            this.commetText = '';
            this.$emit('update-comment-list');
            this.comeBack();
          }else{
            this.popupVisible = true;
            setTimeout(()=>{this.popupVisible = false;},1500);
          }
        })
        .catch((error) => {
          this.isLoading = false;
          console.log(error.response.data);
          Indicator.close();
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
    .popup{
      width: 100%;
      height: 0.86rem;
      background: rgba(0,0,0,0.6);
      text-align: center;
      color: white;
      line-height: 0.86rem;
    }
  textarea { 
    resize : none;
    &::-webkit-input-placeholder {
      color:rgb(173,173,173);
      text-indent: 1em;
    }
  }
.commentCon{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgb(242,242,242);
  .commetText{
    box-sizing: border-box;
    margin:0.1rem 0 0.25rem 0;
    height:3.46rem;
    width:100%;
    text-indent: 1em;
    font-size:0.3rem;
    padding:0.35rem 0 0.35rem 0;
  }
  .submitBtn{
    width:6.9rem;
    height:0.88rem;
    margin:0 auto;
    background:rgb(57,166,206);
    border-radius: 0.15rem;
    font-size:0.36rem;
    line-height:0.88rem;
    color:white;
    text-align:center;
  }
}
</style>



