<!-- by 添少 -->
<template>
  <section class="fixed-layout">
    <section ref="fixed-layout-header" class="fixed-layout-header">
      <slot name="header"></slot>
    </section>
    <section ref="fixed-layout-container" class="fixed-layout-container">
      <slot></slot>
    </section>
    <section class="fixed-layout-footer">
      <slot name="footer"></slot>
    </section>
  </section>
</template>

<script>
  export default {
    name: 'fixed-layout',
    create () {},
    mounted () {
      let container = this.$refs['fixed-layout-container']
      container.addEventListener('scroll', (() => {
        let timeOut = 500 / 2 / 2, lastDate = 0, timer = null, fn = e => {
          lastDate = +new Date()
          this.$emit('scroll', {
            scrollWidth: e.target.scrollWidth,
            scrollHeight: e.target.scrollHeight,
            scrollLeft: e.target.scrollLeft,
            scrollTop: e.target.scrollTop,
            headerHeight: this.$refs['fixed-layout-header'].clientHeight,
          }, e)
        }
        return e => {
          clearTimeout(timer)
          if (+new Date() - lastDate >= timeOut) {
            fn(e)
          } else {
            timer = setTimeout(fn.bind(this, e), timeOut)
          }
        }
      })())
    }
  }
</script>

<style lang="scss" scoped>
  .fixed-layout {
    height: 100vh;
    display: flex;
    flex-direction: column;

    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }

  .fixed-layout-header {
    flex: 0 0 auto;
  }

  .fixed-layout-container {
    position: relative;
    overflow: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    flex: 1;
  }

  .fixed-layout-footer {
    flex: 0 0 auto;
  }
</style>



