<template>
  <div class="loadmoreTips">
    <template v-if='type=="all"'>
      已经加载到底部啦^_^
    </template>
    <template v-else>
      <img src="../../assets/images/loading.gif" alt=""> 加载中...
    </template>
  </div>
</template>
<script>
  export default {
    name: '',
    props: {type:{
      default:'more',
    }},
    data () {
      return {
      }
    },
    watch: {},
    computed: {}
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  .loadmoreTips {
    width:100%;
    font-size:0.28rem;
    padding: 0.28rem 0;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .loadmoreTips>img{
    width: 0.5rem;
  }
</style>


